.main-footer {
  width: 100%;
  background-color: #030a1a;
  position: relative;
}

.main-footer .logo {
  margin-top: 10px;
  width: 88px;
  display: block;

  @include vp-1199 {
    margin-top: 6px;
    width: 82px;
  }

  @include vp-767 {
    position: absolute;
    top: 46px;
    left: 12px;
    margin-top: 0;
  }

  @include hover-focus {
    opacity: 0.7;
  }

  @include active {
    opacity: 0.3;
  }

  &:focus-visible {
    opacity: 0.7;
    outline: none;
    border: none;
  }
}

.main-footer .container {
  padding: 36px 96px 30px 106px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  @include vp-1199 {
    padding: 40px 36px 54px 45px;
  }

  @include vp-767 {
    padding: 40px 10px 54px;
  }
}
